@import "../../common/styles/sass/variabls";

.skill {
  width: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  flex-wrap: wrap;
  margin-bottom: 15px;
  z-index: 99;
  .icon {
    background-size: contain;
    background-position: 50%;
    background-repeat: no-repeat;
    position: relative;

    height: 100px;
    width: 100%;

    z-index: 99;

  }

  .skill,h3 {
    color: $textColor;
  }

  .description {
    word-break: break-all;
    word-wrap: break-word;
    color: $textColor;
  }

}

